.App {
  width : 89vw;
  height : 100%;
  position: fixed;
  top: 0;
  left: 2px;
  padding: 30px 60px;
  display: -ms-flexbox;
  display: flex;
}

.fullscreen {
  width : 100%;
  height : 90%;
  font-size: 22px;
  border: transparent !important;
  outline: none !important;
}

::-webkit-scrollbar {
  width: 0;
  display: none;
}



